import styled from 'styled-components';
import {
  CELL_PADDING,
  DEFAULT_BORDER_WIDTH,
  DEFAULT_FONT_WEIGHT,
  HEADER_BORDER_WIDTH,
  HEADER_FONT_WEIGHT,
  LARGE_CELL_FLEX,
  SMALL_CELL_FLEX,
} from '../../../../shared/styles/constants';

export const StyledWrapper = styled.div`
  display: flex;
  align-items: center;
  flex: ${props => (props.isLarge ? LARGE_CELL_FLEX : SMALL_CELL_FLEX)};
  font-weight: ${props =>
    props.isHeader ? HEADER_FONT_WEIGHT : DEFAULT_FONT_WEIGHT};
  padding-top: ${CELL_PADDING}px;
  padding-bottom: ${CELL_PADDING}px;
  border-bottom-width: ${props =>
    props.isHeader ? HEADER_BORDER_WIDTH : DEFAULT_BORDER_WIDTH}
  border-bottom-style: solid;
  border-bottom-color: #dedede;
`;



